        <brand-control-desktop :name="BrandControlDesktop.name" :descriptions="BrandControlDesktop.descriptions" :button="BrandControlDesktop.button" :tags="BrandControlDesktop.tags" :link="BrandControlDesktop.link" @mouse="hover($event)"/>
      </div>
      <div class="band-card__image">
        <transition name="default">
          <brand-image-category key="category" v-if="showBigImage" :src="BrandImage2.src" :alt="BrandImage2.alt" :status="animationStatus" />
          <brand-image class="band-card__image-item" key="default" v-else  :src="BrandImage.src" :alt="BrandImage.alt" />
        </transition>
      </div>
    </div>
    <div class="band-card__footer">
      <div class="tags">
        <tags-mobile :tags="BrandControlDesktop.tags" @mouse="hover($event)" />
      </div>
      <div class="popular-products">
        <h4 class="popular-products__title">{{ popularProductsTitle }}</h4>
        <popular-products-slider class="products" />
      </div>
      <div class="button-link">
        <button-link class="button" :link="BrandControlDesktop.button.url" :name="BrandControlDesktop.button.name" />
      </div>
    </div>
  </div>
</template>

<script>

import BrandImage from '@/components/brand/BrandImage'
import BrandControlDesktop from '@/components/brand/BrandControlDesktop'
import TagsMobile from '@/components/brand/TagsMobile'
import PopularProductsSlider from '@/components/brand/PopularProductsSlider'
import ButtonLink from '@/components/core/ButtonLink'

import BrandImageCategory from '@/components/brand/BrandImageCategory'

export default {
  name: 'brand-card',
  props: {
    reverse: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    animationStatus: false,
    BrandControlDesktop: {
      name: 'Converse',
      descriptions: 'Взяв свое начало с обычных баскетбольных площадок, кроссовки Converse ныне достигли культового статуса. ',
      button: {
        name: 'Все товары бренда',
        url: 'error'
      },
      link: {
        name: 'Все товары →',
        url: 'error'
      },
      tags: [
        {
          name: 'Высокие кеды',
          key: 'High-top sneakers'
        },
        {
          name: 'Низкие кеды',
          key: 'Low top sneakers'
        },
        {
          name: 'На платформе',
          key: 'On the platform'
        },
        {
          name: 'Одежда',
          key: 'clothing'
        },
        {
          name: 'Рюкзаки',
          key: 'Backpacks'
        },
        {
          name: 'Головные уборы',
          key: 'Hats'
        }
      ]
    },
    BrandImage: {
      src: require('@/assets/brand/brand_img-01.jpg'),
      alt: 'image converse'
    },
    BrandImage2: {
      src: require('@/assets/brand/brand_img-02.jpg'),
      alt: 'image converse'
    },
    popularProductsTitle: 'Популярные товары',
    showBigImage: null
  }),
  components: {
    BrandImage,
    BrandControlDesktop,
    TagsMobile,
    PopularProductsSlider,
    ButtonLink,
    BrandImageCategory
  },
  computed: {

  },
  methods: {
    hover (event) {
      if (event) {
        this.animationStatus = true
        this.showBigImage = event
        return
      }
      this.animationStatus = false
      this.showBigImage = event
    }
  }
}
</script>

