<template>
  <div class="banner-slide w-100">
    <div class="banner">
      <agile @after-change="curentDot($event)" ref="carousel" :options="agile" class="banner-slide__wrapper">
        <div class="slide" v-for="(item, index) in slider" :key="index">
          <banner :options="item" />
        </div>
      </agile>
      <div class="control-bar flex">
        <button class="control-bar__button left no-mobile" @click="prev">
          <v-icon>icon-pointer-left</v-icon>
        </button>
        <div class="control-bar__space flex">
          <div class="control-bar__dot" :class="{ 'active': currentSlide === 0 }"></div>
          <div class="control-bar__dot" :class="{ 'active': currentSlide > 0 &&  currentSlide < slider.length - 1 }"></div>
          <div class="control-bar__dot" :class="{ 'active': currentSlide === slider.length - 1 }"></div>
        </div>
        <button class="control-bar__button right no-mobile" @click="next">
          <v-icon>icon-pointer-right</v-icon>
        </button>
      </div>
    </div>
  </div>
</template>

<script>

import { VueAgile } from 'vue-agile'
import Banner from '@/components/banner/Banner'

export default {
  name: 'banner-slide',
  components: {
    agile: VueAgile,
    Banner
  },
  data: () => ({
    currentSlide: 0,
    agile: {
      navButtons: false,
      dots: false
    },
    slider: [
      {
        image: require('@/assets/banner/img-01.jpg'),
        title: 'Летние коллекции',
        code: 'ЛУЧШЕДОМА',
        button: {
          name: 'Посмотреть товары',
          link: 'error'
        }
      },
      {
        image: require('@/assets/banner/img-01.jpg'),
        title: 'Летние коллекции ',
        code: 'ЛУЧШЕДОМА',
        button: {
          name: 'Посмотреть товары',
          link: 'error'
        }
      },
      {
        image: require('@/assets/banner/img-01.jpg'),
        title: 'Летние коллекции',
        code: 'ЛУЧШЕДОМА',
        button: {
          name: 'Посмотреть товары',
          link: 'error'
        }
      },
      {
        image: require('@/assets/banner/img-01.jpg'),
        title: 'Летние коллекции',
        code: 'ЛУЧШЕДОМА',
        button: {
          name: 'Посмотреть товары',
          link: 'error'
        }
      }
    ]
  }),
  methods: {
    curentDot (event) {
      this.currentSlide = event.currentSlide
    },
    next () {
      this.$refs.carousel.goToNext()
    },
    prev () {
      this.$refs.carousel.goToPrev()
    }
  }
}
</script>

